import { Box, Heading, Image, Text, Link } from '@chakra-ui/react'
import { useEffect, useRef, useState } from 'react'
import styles from '../CSS/Home.module.css';
// import { useSetText } from '../customHooks/useSetText';

export function Home() {

  const a = useRef(null)

  const greet = useRef(null);
  const from = useRef(null);
  const prateek = useRef(null);

  const fStack = useRef(null);
  const web = useRef(null);
  const dev = useRef(null)



  useEffect(() => {


    // let wassup = false;
    let greeting = false;
    setTimeout(() => {

      greet.current.style.filter = "blur(0px)";
      from.current.style.filter = "blur(0px)";
      prateek.current.style.filter = "blur(0px)";
      // hello = true;

    }, 500);

    let id = setInterval(() => {

      console.log("set interval running")
      if (greeting === false) {
        setTimeout(() => {

          fStack.current.style.filter = "blur(25px) contrast(100)";
          fStack.current.style.opacity = "0";

          web.current.style.filter = "blur(25px) contrast(100)";
          web.current.style.opacity = "0";

          dev.current.style.filter = "blur(25px) contrast(100)";
          dev.current.style.opacity = "0";
        }, 900)

        greet.current.style.filter = "blur(0px) contrast(20)"
        greet.current.style.opacity = "1";

        from.current.style.filter = "blur(0px) contrast(20)"
        from.current.style.opacity = "1";

        prateek.current.style.filter = "blur(0px) contrast(20)"
        prateek.current.style.opacity = "1";

        greeting = true;
        // wassup = false;

      } else {






        fStack.current.style.filter = "blur(0px) contrast(20)";
        fStack.current.style.opacity = "1";

        web.current.style.filter = "blur(0px) contrast(20)";
        web.current.style.opacity = "1";

        dev.current.style.filter = "blur(0px) contrast(20)";
        dev.current.style.opacity = "1";

        setTimeout(() => {

          greet.current.style.filter = "blur(25px) contrast(100)"
          greet.current.style.opacity = "0";

          from.current.style.filter = "blur(25px) contrast(100)"
          from.current.style.opacity = "0";

          prateek.current.style.filter = "blur(25px) contrast(100)"
          prateek.current.style.opacity = "0";
        }, 900)

        greeting = false;
      }
    }, 5000)


    return () => {


      clearInterval(id)
    }

  }, [greet])


  // -------------------------hover to display text ------------------------------------------





  // -------------------------------------------------return statement---------------------------------------------------------------------------
  return (

    <Box
      ref={a}
      data-aos="fade-down-right"
      id="home"
      w="100%"
      className={styles.homeMainBox}
      h="auto"

      // border="10px solid black"
      pb="100px"
    // position="relative"
    >


      <Box
        display="flex"
        justifyContent="space-evenly"
        alignItems="center"
        id={styles.childofMain}
      >
        {/* <Image  clipPath="inset(90px 0px 0px 50px);" zIndex="0" opacity="0" ml="400px" mb="50px" width="25%" position="absolute" src="https://media.istockphoto.com/id/1298995850/vector/map-of-the-city-of-lucknow-india-1850s-19th-century.jpg?s=1024x1024&w=is&k=20&c=vQy5TKOoh-JBlPKsZQ_5CNmUr9NwJusBaO8RHL_r1lo=" /> */}

        <Box id={styles.bgBox} bg="brand.secondGray"  ></Box>

        <Box

          w="100%"
          h="500px"
          display="flex"
          justifyContent="space-between"
          alignItems="center"
          id={styles.box}
          // border="1px solid red"
          mt="100px"
          p="40px"
          // mb="100px"
          zIndex="2"
        >

          {/* //---------------------------------------welcome Headings------------------------------------------------------------------------------         */}

          <Box id={styles.textEffectBox}
            // border="1px solid black"
            h="500px" width="660px" justifyContent="center" align="left" zIndex="2">

            <Box position="relative" top="50px" id={styles.greetings} >
              <Heading ref={greet} className={styles.g} fontSize="120px" fontFamily="Gruppo" fontWeight="bold" >
                Greetings !!
              </Heading>
              <Heading ref={from} className={styles.g} fontSize="120px" fontWeight="bold" fontFamily="Gruppo">
                From
              </Heading>
              <Heading
                ref={prateek} className={styles.g}

                fontSize="120px"
                fontWeight="bolder"
                fontFamily="Gruppo"
              >
                Prateek...
              </Heading>
            </Box>


            <Box position="relative" top="-380px" id={styles.fullStack} >

              <Heading ref={fStack} className={styles.fs} fontSize="120px" fontFamily="Gruppo"  >Full Stack</Heading>

              <Heading ref={web} className={styles.fs} fontSize="120px" fontFamily="Gruppo"  >Web</Heading>

              <Heading ref={dev} className={styles.fs} fontSize="120px" fontFamily="Gruppo"  >Developer...</Heading>

            </Box>

          </Box>



          {/* ------------------------------------------------------------------------------------------------------------------------------------------------ */}

          <Box
            // border="1px solid black"
            // w="auto"
            // p="20px"
            // display="flex"
            zIndex="3"
            id={styles.bioDataBox}

          >

            <Box
              // border="1px solid black" 
              w="600px"
              display="flex"
              gap="5px"
              id={styles.summaryBox}
            >

              <Text
                // border="1px solid yellow"
                h="180px"
                w="360px"
                mt="165px"
                textAlign="justify"
                fontSize="18px"
                fontFamily="Gruppo"
                fontWeight="bold"
                color="brand.lightGray"
                id={styles.summary1}
              >
                An aspiring <span style={{ backgroundColor: "#fc6703", color: "#1f1f1f", fontWeight: "bold" }} >Full Stack Web Developer</span>  with an eye for detail and
                design. Very thoughtful in creating robust and elegant websites
                using <span style={{ backgroundColor: "#fc6703", color: "#1f1f1f" }} ><b>MERN</b></span> stack. Also adept in
                <span style={{ backgroundColor: "#fc6703", color: "#1f1f1f" }} ><b> HTML, CSS, JS.</b></span>
              </Text>
              <Image
                // ml="150px"
                // w="20%"
                // mb="50px"

                src="/prateek.jpg"
                alt="prateek.jpg"
                id={styles.image}
                zIndex="4"
              />
            </Box>

            <Text
              // border="1px solid green" 
              id={styles.summary2} color="brand.lightGray" align="left" fontWeight="bold" fontSize="18px" w="600px" mt="5px" fontFamily="Gruppo">
              Passionate about building a fine user experience. <span style={{ backgroundColor: "#fc6703", color: "#1f1f1f" }} ><b>Genuinely</b></span> looking
              forward to the experience of working in an organization.
            </Text>
          </Box>





        </Box>
      </Box>



      <Box id={styles.pSumry} display="none" m="auto"  >
        <Text id={styles.pSumryText} w="94%" p="10px" pt="40px" textAlign="left" fontSize="20px" fontFamily="Gruppo" fontWeight="bold" borderRadius="10px" m="auto" borderBottom="5px solid #1A1A1D" borderRight="5px solid #1A1A1D" >An aspiring <span style={{ backgroundColor: "#fc6703", color: "#1f1f1f", fontWeight: "bold" }} >Full Stack Web Developer</span>  with an eye for detail and
          design. Very thoughtful in creating robust and elegant websites
          using <span style={{ backgroundColor: "#fc6703", color: "#1f1f1f" }} ><b>MERN</b></span> stack. Also adept in
          <span style={{ backgroundColor: "#fc6703", color: "#1f1f1f" }} ><b> HTML, CSS, JS.</b></span>
          Passionate about building a fine user experience. <span style={{ backgroundColor: "#fc6703", color: "#1f1f1f" }} ><b>Genuinely</b></span> looking
          forward to the experience of working in an organization.
        </Text>
      </Box>

    </Box>





  )
}
