import { Box, Heading, Link, Button, Text } from '@chakra-ui/react'
import { ChevronDownIcon, HamburgerIcon } from '@chakra-ui/icons'
import { IconButton } from '@chakra-ui/react'
import styles from '../CSS/Navbar.module.css'

import {
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
  MenuItemOption,
  MenuGroup,
  MenuOptionGroup,
  MenuDivider,
} from '@chakra-ui/react'



export function Navbar() {

  const openNewTab = () => {

    window.open(
      "https://drive.google.com/file/d/1VJzvHdisgV5td0WpKfovrptjiMuI6cDB/view?usp=sharing"
    )
  }

  return (
    <Box zIndex="5" position="fixed" width="100vw" h="auto" border="5px solid #1A1A1D" id={styles.NavMainBox} >
      <Box
        id={styles.container}
        // border="1px solid white"
        bg="brand.secondGray"
        h="50px"
        w="auto"

        display="flex"
        justifyContent="space-between"

        color="brand.lightGray"


      // boxShadow="rgba(149,7,64, 0.45) 0px 0px 10px 10px;"
      >
        <Box pl="20px" display="flex" alignItems="center" >
          <Heading fontFamily="Gruppo" fontSize="40px" id={styles.heading} >Prateek Bhattacharya</Heading>
        </Box>

        <Box display="flex" justifyContent="flex-end" alignItems="center" pr="15px" gap="10px" w="60%" id={styles.hamburger}  >

          <Link href="#home" >    <Text fontSize="22px" fontWeight="bold" >Home</Text> </Link>
          <Link href="#about">   <Text fontSize="22px" fontWeight="bold" >About-Me</Text> </Link>
          <Link href="#skills">  <Text fontSize="22px" fontWeight="bold" >Skills  </Text>      </Link>
          <Link href="#project"> <Text fontSize="22px" fontWeight="bold" >Projects</Text>    </Link>
          {/* <Link href="#calendar">  <Text fontSize="lg" fontWeight="bold">GitHub Calendar  </Text>    </Link> */}
          <Link href="#contact"> <Text fontSize="22px" fontWeight="bold" >Contact </Text>     </Link>
          <Link href="/Prateek-Bhattacharya-Resume.pdf" download="/Prateek-Bhattacharya-Resume.pdf" onClick={openNewTab} > <Text fontSize="22px" fontWeight="bold" >Resume </Text>     </Link>
        </Box>

        <Box fontWeight="bold" id={styles.menu}>
          <Menu>
            <MenuButton bg="brand.lightGray" mt="5px" mr="5px" as={IconButton} aria-label='Options' icon={<HamburgerIcon color="brand.900" />} />

            <MenuList   >
              <Link href="#home"  > <MenuItem  _hover={{backgroundColor:"brand.lightGray"}} >  <Text fontWeight="bold" color="brand.900" >Home</Text> </MenuItem></Link>
              <Link href="#about"> <MenuItem _hover={{backgroundColor:"brand.lightGray"}}  >  <Text fontWeight="bold" color="brand.900" >About Me</Text> </MenuItem></Link>
              <Link href="#skills">  <MenuItem _hover={{backgroundColor:"brand.lightGray"}}  >  <Text fontWeight="bold" color="brand.900" >Skills</Text> </MenuItem></Link>
              <Link href="#project"> <MenuItem _hover={{backgroundColor:"brand.lightGray"}}   >  <Text fontWeight="bold" color="brand.900" > Projects</Text>  </MenuItem></Link>
              <Link href="#contact"> <MenuItem _hover={{backgroundColor:"brand.lightGray"}}  >  <Text fontWeight="bold" color="brand.900" >Contact</Text> </MenuItem></Link>
              <Link href="/Prateek-Bhattacharya-Resume.pdf" download="/Prateek-Bhattacharya-Resume.pdf" onClick={openNewTab} >  <MenuItem _hover={{backgroundColor:"brand.lightGray"}}   >  <Text fontWeight="bold" color="brand.900" >Resume</Text> </MenuItem></Link>
            </MenuList>
          </Menu>
        </Box>


      </Box>
    </Box>
  )
}
